html{
  background-color: #2A60FF;
  display: contents;
  background-image: url(./keheninggan.jpg);
    width: 100%;
    background-repeat: no-repeat;
    background-size: 100%;
}

h1 {
  font-size: 75px;
  margin: 0;
  background-color: #ff9f06;
  color: white;
  padding: 10px;
  border-radius: 10px solid #000000;
}

h2 {
  font-size: 18px;
  color: #ffffff;
}

.center {
  text-align: center;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  /* vertical-align: center; */
}

.player {
  margin: 10px;
  width: 120px;
  height: 120px;
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  color: #ffffff;
}

.score {
  padding: 1px;
  font-size: small;
  font-weight: bold;
  background-color: #ff9f06;
}

.action {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  color: #ff9f06;
}

.round-btn {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  margin: 2px;
  cursor: pointer;
  background-color: #ff9f06;
  color: #ffffff;
}

.round-btn:hover {
  background-color: #ff9f06;
}